#primary-container {
  width: 960px;
  display: flex;
  padding: 50px 15px;
  margin: 0 auto; }

#primary-navigation {
  flex: 0 0 260px;
  padding: 15px;
  margin-right: 15px; }
  #primary-navigation h1 {
    font-weight: 300;
    font-size: 1.714rem;
    line-height: 1.714rem;
    padding: 0 15px;
    margin: 15px 0; }
  #primary-navigation ul {
    padding: 0; }
    #primary-navigation ul li {
      display: block;
      margin-top: 2px; }
      #primary-navigation ul li a {
        display: block;
        width: 100%;
        padding: 8px 15px;
        border-radius: 4px;
        font-weight: 300; }
        #primary-navigation ul li a:hover {
          background-color: #e6f5fa;
          color: #137499; }
      #primary-navigation ul li.category {
        padding: 0 15px;
        margin: 20px 0 5px;
        font-size: .857rem;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #969696; }
      #primary-navigation ul li.logoff {
        margin: 20px 0 0; }

#primary-content {
  flex: 1 1 auto;
  padding: 15px;
  margin-left: 15px; }
  #primary-content header {
    border-bottom: 2px solid #f0f0f0;
    margin-bottom: 30px;
    padding-bottom: 15px;
    display: flex; }
    #primary-content header .headings {
      flex: 1 0 auto; }
    #primary-content header .buttons {
      display: flex;
      flex: 0 1 auto;
      align-items: flex-end; }
    #primary-content header h2 {
      color: #aeaeae;
      text-transform: uppercase;
      font-size: 0.857rem;
      line-height: 0.857rem;
      font-weight: 400;
      letter-spacing: 1px;
      margin: 15px 0 10px 0; }
    #primary-content header h1 {
      font-size: 2.143rem;
      line-height: 2.143rem;
      font-weight: 300;
      margin: 0; }

button, [role=button] {
  padding: 6px 12px;
  display: inline-block;
  vertical-align: bottom;
  background-color: transparent;
  outline: none;
  border: 1px solid #1eaadc;
  border-radius: 4px;
  text-align: center;
  color: #1eaadc; }
  button:hover, [role=button]:hover {
    background-color: #1eaadc;
    color: white; }

.button-group {
  display: inline-block;
  vertical-align: bottom;
  overflow: hidden; }
  .button-group button, .button-group [role=button] {
    float: left;
    display: block;
    border-radius: 0;
    margin-left: -1px; }
    .button-group button:first-child, .button-group [role=button]:first-child {
      border-radius: 4px 0 0 4px;
      margin-left: 0; }
    .button-group button:last-child, .button-group [role=button]:last-child {
      border-radius: 0 4px 4px 0; }

table {
  width: 100%; }
  table tr th {
    padding: 0 25px 8px 25px;
    text-align: left;
    font-weight: 500; }
    table tr th:first-of-type {
      padding-left: 0; }
    table tr th:last-of-type {
      padding-right: 0; }
    table tr th.span {
      width: 99%; }
    table tr th.shrink {
      width: 1%; }
  table tr td {
    padding: 8px 25px;
    border-top: 1px solid #eeeeee;
    white-space: nowrap;
    font-weight: 300; }
    table tr td:first-of-type {
      padding-left: 0; }
    table tr td:last-of-type {
      padding-right: 0; }
  table tr:last-of-type td {
    padding-bottom: 0; }

form {
  display: flex;
  flex-flow: column nowrap; }

label {
  margin-bottom: 30px; }
  label span {
    display: block;
    padding-bottom: 5px; }

input, select {
  display: block;
  width: 100%;
  height: 35px;
  padding: 6px 12px;
  border: 1px solid #d5d5d5;
  background-color: #ffffff;
  border-radius: 4px;
  outline: none;
  font-weight: 300;
  transition: border-color 100ms; }
  input:focus, select:focus {
    border-color: #1997c6; }

#batch-show .button-group {
  width: 100%; }
  #batch-show .button-group button {
    width: 50%; }
